@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: normal;
  font-display: swap;
  src: url("/build/fonts/Roboto-Regular.ttf"), url("/build/fonts/Roboto-SemiBold.ttf");
}
* {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
}

*::-webkit-scrollbar {
  width: 12px;
}

*::-webkit-scrollbar-track {
  background: rgb(30, 30, 30);
}

*::-webkit-scrollbar-thumb {
  background-color: gray;
  border-radius: 20px;
  border: 3px solid rgb(30, 30, 30);
}

html {
  font-family: "Roboto", sans-serif;
}

body {
  background-color: #303030;
}

a {
  text-decoration: none;
  color: #ffffff;
}

img {
  width: 100%;
  height: auto;
}

.container {
  width: 60%;
  margin: 0 auto;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 100;
}
.header .logo {
  filter: invert(100%);
  width: 2rem;
  margin: 1rem;
  transition: transform 0.5s ease;
}
.header .logo:hover {
  transform: scale(1.1);
}
.header .menu {
  display: none;
  width: 40px;
}

.nav a {
  color: #ffffff;
  font-weight: 700;
  margin: 1rem;
  transition: color 0.4s ease-out;
}
.nav a:hover {
  color: #f5deb3;
}

.hero {
  background-image: url("/build/images/background.webp");
  background-attachment: fixed;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 50rem;
  margin-bottom: 5rem;
  box-shadow: 0 0 0.9rem #000000;
}

.hero-shadow {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.3);
  color: #ffe8be;
  width: 100%;
  height: 100%;
}
.hero-shadow h2 {
  font-weight: 900;
  font-size: 4rem;
}
.hero-shadow p {
  font-size: 1.2rem;
  font-weight: 700;
  text-decoration: line-through 5px;
}

.gallery {
  background-color: rgba(0, 0, 0, 0.1);
  text-align: center;
  font-size: 1.1rem;
  padding: 1.5rem;
  border-radius: 30px;
  margin-bottom: 9rem;
}
.gallery h2 {
  font-size: 3rem;
}
.gallery h2,
.gallery p {
  margin: 0.5rem;
  color: #ffffff;
}

.gallery-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
  margin-top: 15px;
}
.gallery-grid img {
  border-radius: 10px;
  cursor: pointer;
  transition: transform 0.2s ease;
}
.gallery-grid img:hover {
  transform: scale(1.02);
}

.overlay {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.9);
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
}
.overlay img {
  display: block;
  width: 70%;
}

.contact h2 {
  text-align: center;
  font-size: 2.5rem;
  color: #ffffff;
  margin-bottom: 1rem;
}

.form {
  background-color: rgba(0, 0, 0, 0.1);
  width: min(40rem, 100%);
  margin: 0 auto;
  padding: 2rem;
  border-radius: 1rem;
}

.field {
  margin-bottom: 1rem;
}
.field textarea {
  height: 12rem;
}
.field label {
  color: #ffffff;
  font-weight: bold;
  margin-bottom: 0.5rem;
  display: block;
}

.input-text {
  width: 100%;
  border: none;
  padding: 1.3rem;
  border-radius: 0.5rem;
  font-family: inherit;
  font-size: 100%;
}

.button {
  background-color: #998151;
  color: #ffffff;
  padding: 1rem 2rem;
  font-size: 1.2rem;
  font-weight: bold;
  border-radius: 2rem;
  border: none;
  line-height: 1;
  cursor: pointer;
  transition: transform 0.5s ease;
}
.button:hover {
  transform: scale(1.05);
}

.align {
  display: flex;
  justify-content: center;
}

.footer {
  padding: 3rem 0;
  background-color: rgba(0, 0, 0, 0.1);
  margin-top: 5rem;
}
.footer p {
  text-align: center;
  font-size: 1.2rem;
  font-weight: 700;
  color: #ffe8be;
}

@media only screen and (width <= 992px) {
  .container {
    width: 90%;
  }
  .gallery {
    padding: 1rem;
  }
  .gallery-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
  .overlay img {
    width: 100%;
  }
  .form {
    width: min(30rem, 100%);
  }
}
@media only screen and (width <= 576px) {
  .header {
    flex-direction: column;
    background-color: rgba(0, 0, 0, 0.1);
  }
  .header div {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 90%;
  }
  .header .logo {
    width: 3rem;
  }
  .header .menu {
    display: flex;
  }
  .nav {
    display: none;
  }
  .nav.show {
    display: flex;
    flex-direction: column;
  }
  .nav a {
    text-align: center;
    margin: 10px;
  }
  .container {
    width: 90%;
  }
  .gallery {
    padding: 1rem;
  }
  .gallery-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 0.5rem;
  }
  .form {
    padding: 1.5rem;
  }
  .input-text {
    padding: 1rem;
  }
}